/**
 * XSS - 
 * // ===
 * 1. 
 * 2. 
   2.1 
   2.2 cookie CSRF 
   2.3 
   2.4 
 * 3. 
   3.1   uri
   3.2 DB
   3.3 DOM-baseHTML
 * 4.
   4.1 HTML
     <div>                        <div>
     {content}       ==         <script > xxxx </script>
     </div>                       </div>
   4.2 HTML
     <img src={item.src} />   ==    <img src="1" onerror="alert(1);" />
   4.3 jsjs
      var data = "#{data}"    ==     var data = "hello";alert(1);"";
   4.4 HTMLXSS
 * 5. 
   5.1 : X-XSS-Protection,  HTML  HTML
   5.2 
       html:
       < : '&lt;'
       > : '&gt;'
       html:
       & : '&amp;'
       " : '&quto;'
      ' : '&#39;'
   5.3 js: `\`
   5.4 
      , `<script>`, `javascript:;`, `onerror`
      , `img: src, alt, title`,  / xss
   5.5 CSP (Content Security Policy) - 
   5.6 httpOnlycookie
 * === //
 */

/**
 * html
 * @param html
 * @return {*}
 */
export const replaceHtml = (html) => {
  if (html === '') {
    return ''
  }
  html.replace(/&/g, '&amp;')
    .replace(/</g, '&lt;')
    .replace(/>/g, '&gt;')
    .replace(/"/g, '&quot;')
    .replace(/'/, '&#39;')
  return html
}
  /**
   * javascript
   * @param html
   * @return {*}
   */
export const replaceJavaScript = (html) => {
  if (html === '') {
    return ''
  }

  html.replace(/\\/g, '\\\\')
    .replace(/"/g, '\\"')
    .replace(/'/g, '\\')

  return html
}