/**
 * XSS - 
 * // ===
 * 1. 
 * 2. 
   2.1 
   2.2 cookie CSRF 
   2.3 
   2.4 
 * 3. 
   3.1   uri
   3.2 DB
   3.3 DOM-baseHTML
 * 4.
   4.1 HTML
     <div>                        <div>
     {content}       ==         <script > xxxx </script>
     </div>                       </div>
   4.2 HTML
     <img src={item.src} />   ==    <img src="1" onerror="alert(1);" />
   4.3 jsjs
      var data = "#{data}"    ==     var data = "hello";alert(1);"";
   4.4 HTMLXSS
 * 5. 
   5.1 : X-XSS-Protection,  HTML  HTML
   5.2 
       html:
       < : '&lt;'
       > : '&gt;'
       html:
       & : '&amp;'
       " : '&quto;'
      ' : '&#39;'
   5.3 js: `\`
   5.4 
      , `<script>`, `javascript:;`, `onerror`
      , `img: src, alt, title`,  / xss
   5.5 CSP (Content Security Policy) - 
   5.6 httpOnlycookie
 * === //
 */

export const reg = {
  /**
   * html
   * @param html
   * @return {*}
   */
  replaceHtml: (html) => {
    if (html === '') {
      return ''
    }
    html.replace(/&/g, '&amp;')
      .replace(/</g, '&lt;')
      .replace(/>/g, '&gt;')
      .replace(/"/g, '&quot;')
      .replace(/'/, '&#39;')
    return html
  },
  /**
   * javascript
   * @param html
   * @return {*}
   */
  replaceJavaScript: (html) => {
    if (html === '') {
      return ''
    }
    html.replace(/\\/g, '\\\\')
      .replace(/"/g, '\\"')
      .replace(/'/g, '\\')
     
    return html
  }
}

let strategies = {
  /**
   * 
   * @param value
   * @param errorMsg
   * @return {*}
   */
  isEmpty (value, errorMsg) {
    let len = value.trim().length
    if (len === 0) {
      return errorMsg
    }
  },

  /**
   * 
   * @param value
   * @param errorMsg
   * @return {*}
   */
  isEmail (value, errorMsg) {
    const reg = /^[a-zA-Z\d][\w\.-]{3,17}@[a-zA-Z\d][\w\.-]+\.[a-z]{2,5}$/g
    if (!reg.test(value)) {
      return errorMsg
    }
  },
  /**
   * 
   * @param value
   * @param errorMsg
   * @return {*}
   */
  isWebsite (value, errorMsg) {
    const reg = /^https?:\/\/[^\s]+$/
    if (!reg.test(value)) {
      return errorMsg
    }
  }
}

export class Validator {
    constructor () {
      this.cache = []
    }

  /**
   * 
   * @param formEle
   * @param rules
   */
    add (formEle, rules) {
      for (let rule of rules) {
        let strategyArray = rule.strategy.split(':')
        let errorMsg = rule.errorMsg

        this.cache.push(() => {
          let strategy = strategyArray.shift()
          strategyArray.unshift(formEle.value.trim())
          strategyArray.push(errorMsg)
          return strategies[strategy].apply(formEle, strategyArray)
        })
      }
    }

  /**
   * 
   * @return {*}
   */
  start () {
      for (let validatorFunc of this.cache) {
        const errorMsg = validatorFunc()
        if (errorMsg) {
          return errorMsg
        }
      }
    }
}
