import React, { Component } from 'react'

// === connect()  React  store  === //
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import PropTypes from 'prop-types'
import PureRenderMixin from 'react-addons-pure-render-mixin'

import Header from '../../components/Header'
import ArticleList from '../../components/ArticleList'
import Pagination from '../../../../plugin/pagination';

import { actions as ArticleReducer } from '../../../../redux/reducer/articleReducer'


import './style.css'

const { get_all_articles } = ArticleReducer

class Home extends Component {
  constructor (props) {
    super(props)

    this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate
    this.pageOnChange = this.pageOnChange.bind(this)
  }

  /**
   * 
   * @param page
   * @param pagesize
   */
  pageOnChange (page, pagesize) {
    this.props.get_all_articles({
      pageNum: page,
      pageSize: pagesize
    })
  }

  render () {
    const { articleList, total } = this.props

    return (
      <div className="container my--blog">
        <Header/>
        <div className="main">
          <div className="main-inner">
            <ArticleList data={ articleList }/>
            <div className="pagination-wrapper">
              <Pagination onChange={ this.pageOnChange } defaultCurrent={ 1 } total={ parseInt(total) } defaultPageSize={ 5 }/>
            </div>
          </div>
        </div>
      </div>
    )
  }

  componentDidMount () {
    this.props.get_all_articles({
      pageNum: 1,
      pageSize: 5
    })
  }
}

// 
Home.defaultProps = {
  articleList: [],
  total: 0
}

Home.propTypes = {
  articleList: PropTypes.array,
  total: PropTypes.number
}

/**
 *
  // ===
   mapStateToProps(state)
   1Reduxprops
   2React  Redux 
  === //
 */

function mapStateToProps (state) {
  const { articleList, total } = state.articles

  return {
    articleList,
    total
  }
}

/**
 *
 // ===
   mapDispatchToProps(storedipatch, [this.props])
   1connect
   2 connect  actionCreator  dispatch 
      bindActionCreators  props 
 === //
 */
function mapDispatchToProps (dispatch) {
  return {
    get_all_articles: bindActionCreators(get_all_articles, dispatch)
  }
}

// ===  connect(mapStateToProps, mapDispatchToProps, mergeProps, options = {})  === //
export default connect (
  mapStateToProps,
  mapDispatchToProps
)(Home)